.single-blog {
    padding-top: 112px;
    padding-bottom: 40px;
    max-width: 1080px;

    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        font-family: Turbine, sans-serif;
        font-style: normal;
        font-weight: 700;
    }

    h1 {
        font-size: 80px;
        line-height: 1.37;

        @media screen and (max-width: 768px) {
            font-size: 36px;
        }
    }

    h2 {

    }

    h3 {
        font-size: 52px;
        line-height: 1.15;

        &.single-blog__custom-date {
            font-weight: 300;

            @media screen and (max-width: 768px) {
                font-size: 18px;
            }
        }
    }

    h4,
    h5,
    h6 {
        line-height: 1.26;
    }

    h4 {
        font-size: 36px;
    }

    h5 {
        font-size: 30px;

        &.single-blog__post-title {
            margin-bottom: 24px;
        }

        &.single-blog__read-more-title {
            font-weight: 300;
        }
    }

    h6 {
        font-size: 26px;
    }

    strong {
        color: #17181A;
        font-family: Turbine, sans-serif;
        font-weight: 700;
        line-height: 1.4;
    }

    //p {
    //    margin-bottom: 24px;
    //}

    &__content-wrap {

    }

    &__header {
        display: flex;
        align-items: center;
        margin-bottom: 24px;

        @media screen and (max-width: 768px) {
            width: 100%;
            justify-content: space-between;
        }
    }

    &__post-category {
        color: #17181A;
        margin-right: 32px;
    }

    &__custom-date {
        color: #CC7510;
        margin-bottom: 0;
    }

    &__hero {
        margin-bottom: 40px;
    }

    &__hero-top {
        display: flex;
        align-items: center;
        background: #445C5A;
        padding: 8px 16px;
    }

    &__tag-icon-wrap {
        margin-bottom: 0;
        margin-right: 16px;
    }

    &__tag-name {
        color: #F3F0EC;
        font-size: 18px;
        font-style: normal;
        font-weight: 500;
        line-height: 1.33;
    }

    &__hero-content-wrap {
        display: flex;
        flex-direction: column;

        &.hero-type-fifty_image {
            flex-direction: row;

            > .hero-content-part {
                flex: 1
            }

            .single-blog__post-img-wrap {
                height: auto;
            }

            @media screen and (max-width: 768px) {
                flex-direction: column;
            }
        }
    }

    &__post-img-wrap {
        width: 100%;
        height: 598px;
        margin-bottom: 0;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__hero-content {
        padding: 40px;
        background: #C7CAC9;
    }

    &__post-title {
        color: #17181A;
        margin-bottom: 24px;
    }

    &__post-hero-text {
        color: #17181A;
        font-size: 20px;
        font-style: normal;
        font-weight: 400;
        line-height: 1.4;
    }

    &__post-text {
        padding-bottom: 40px;
        margin-bottom: 40px;
        border-bottom: 1px solid #17181A;

        h1,
        h2,
        h3,
        h4,
        h5,
        h6 {
            margin-bottom: 16px;
        }

        div {
            strong {
                display: inline-flex;
                margin-bottom: 16px;
            }
        }

        img {
            width: calc(50% - 1rem);
        }

        p {
            &:last-of-type {
                margin-bottom: 0;
            }
        }
    }

    &__read-more-title {
        text-align: center;
        color: #000;
        margin-bottom: 40px;
    }

    &__read-more-btn {
        cursor: pointer;
        position: absolute;
        z-index: 2;
        top: 50%;
        transform: translateY(-50%);
        width: max-content;

        &.prev {
            left: -40px;
        }

        &.next {
            left: auto;
            right: -40px;
        }
    }
}
